@import url(https://fonts.googleapis.com/css?family=Noto+Sans:400,700|Source+Code+Pro);
*, *::before, *::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body {
  font-family: "Noto Sans", sans-serif;
  font-size: 16px;
  margin: 0;
}
h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
}
.container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.folder {
  margin: 0 0 0 auto;
  font-size: 70%;
  height: 400px;
  min-width: 214px;
}
.files-list {
  max-height: 70vh;
  overflow: auto;
  font-size: 90%;
  min-height: 100%;
  background: rgba(255, 255, 255, 0.9);
}
.files-list .file {
    cursor: pointer;
    padding: 10px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    color: rgba(0, 0, 0, 0.4);
}
.files-list .file .file-new {
      margin: 0 auto;
}
.files-list .file .file-new i {
        color: rgba(0, 204, 0, 0.5);
}
.files-list .file.active {
      outline: 1px solid rgba(0, 204, 0, 0.5);
}
.files-list .file .file-detail {
      display: block;
      margin: 0 4px;
}
.files-list .file .file-detail .title, .files-list .file .file-detail .value {
        display: inline-block;
}
.files-list .file .file-detail .value {
        color: #000;
}
.files-list::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    border-radius: 10px;
    background-color: #fff;
}
.files-list::-webkit-scrollbar {
    width: 6px;
    background-color: #fff;
}
.files-list::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: lightgray;
}
.fa.error {
  color: rgba(204, 0, 0, 0.6);
}
.fa.error:hover {
    color: rgba(204, 0, 0, 0.5);
}
.fa.success {
  color: rgba(0, 204, 0, 0.6);
}
.fa.success:hover {
    color: rgba(0, 204, 0, 0.5);
}
#app-wall {
  min-height: 100vh;
  padding: 5rem 0;
  -webkit-transition: background .4s;
  transition: background .4s;
  background-color: rgba(0, 204, 204, 0.15);
}
#app {
  width: 40rem;
  max-width: 100%;
  height: 400px;
  margin: 0 auto 0 0;
  padding: 0 1rem;
  font-size: 70%;
  -webkit-transition: all .4s;
  transition: all .4s;
}
header {
  position: relative;
  height: 20px;
  line-height: 20px;
  text-align: center;
  font-size: .75em;
  color: rgba(0, 0, 0, 0.8);
  background: lightgray;
  border-radius: 5px 5px 0 0;
}
header .buttons {
    position: absolute;
    top: 6px;
    left: 22px;
    width: 8px;
    height: 8px;
    background: rgba(255, 153, 0, 0.5);
    border-radius: 50%;
    -webkit-box-shadow: -12px 0 0 rgba(204, 0, 0, 0.6), 12px 0 0 rgba(0, 204, 0, 0.5);
            box-shadow: -12px 0 0 rgba(204, 0, 0, 0.6), 12px 0 0 rgba(0, 204, 0, 0.5);
}
.editor {
  position: relative;
  min-height: 100%;
  background: rgba(0, 0, 0, 0.2);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  overflow: hidden;
}
.editor .code,
  .editor .result,
  .editor .settings {
    width: 50%;
    padding: 1rem;
}
.editor .code {
    resize: none;
    border: none;
    font-size: 100%;
    font-family: "Source Code Pro", monospace;
    color: #00cccc;
    background: rgba(0, 0, 0, 0.8);
    outline: none;
    -webkit-transition: color .4s;
    transition: color .4s;
}
.editor .result {
    background: #fff;
}
.editor .result hr {
      border: none;
      border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
.editor .result img {
      max-width: 100%;
      height: auto;
}
.editor .result ul, .editor .result ol {
      margin-left: 0;
      padding-left: 20px;
}
.editor .result ul li, .editor .result ol li {
        margin-left: 0;
}
.editor .alert {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    background-color: rgba(255, 255, 255, 0.85);
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    -webkit-transition: -webkit-transform .4s;
    transition: -webkit-transform .4s;
    transition: transform .4s;
    transition: transform .4s, -webkit-transform .4s;
}
.editor .alert.open {
      -webkit-transform: translateY(0);
              transform: translateY(0);
}
.editor .alert .text {
      border-radius: .4rem;
      padding: 1rem 1rem 1.1rem;
      text-align: center;
      background: #fff;
      color: rgba(0, 0, 0, 0.5);
      line-height: 1.4;
}
.editor .alert .text .options {
        display: inline-block;
}
.editor .alert .text .options > i {
          cursor: pointer;
}
.editor .settings {
    position: absolute;
    top: 0;
    right: 0;
    width: 51%;
    height: 100%;
    color: #fff;
    background: #000;
    -webkit-transform: translateX(101%);
            transform: translateX(101%);
    -webkit-transition: -webkit-transform .4s;
    transition: -webkit-transform .4s;
    transition: transform .4s;
    transition: transform .4s, -webkit-transform .4s;
}
.editor .settings.active {
      -webkit-transform: translateX(0);
              transform: translateX(0);
}
.editor .settings h2 {
      position: relative;
}
.editor .settings h2 .fa {
        position: absolute;
        top: 50%;
        right: 0;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
        -webkit-transition: -webkit-transform .3s;
        transition: -webkit-transform .3s;
        transition: transform .3s;
        transition: transform .3s, -webkit-transform .3s;
        font-size: .85em;
        cursor: pointer;
}
.editor .settings h2 .fa:hover {
          -webkit-transform: translateY(-50%) rotate(-180deg);
                  transform: translateY(-50%) rotate(-180deg);
}
.editor .settings h3 {
      margin-top: 2rem;
      padding: .25rem .2rem .2rem;
      font-weight: normal;
      background: rgba(255, 255, 255, 0.2);
}
footer {
  position: relative;
  height: 20px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  font-size: .75em;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.4);
  border-radius: 0 0 5px 5px;
  -webkit-box-shadow: 0 12px 15px -8px rgba(0, 0, 0, 0.9);
          box-shadow: 0 12px 15px -8px rgba(0, 0, 0, 0.9);
  -webkit-transition: background .4s;
  transition: background .4s;
  padding: 0 7px;
}
footer a {
    text-decoration: none;
    color: rgba(0, 0, 0, 0.4);
}
footer .options {
    float: right;
}
footer .options .fa {
      position: relative;
      padding: 4px;
      font-size: 14px;
      cursor: pointer;
      border-radius: 50%;
      -webkit-transition: color .4s;
      transition: color .4s;
}
footer .options .fa:hover {
        color: rgba(0, 0, 0, 0.8);
}
footer .options .fa.lookme {
        -webkit-animation: blink 1s linear infinite alternate;
                animation: blink 1s linear infinite alternate;
}
@media screen and (max-width: 620px) {
#app-wall {
    padding-top: 2rem;
}
.container {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
.folder {
    width: 40rem;
    max-width: 100%;
    padding: 0 1rem;
    height: 10rem;
    margin-bottom: 4rem;
}
.folder .files-list {
      height: 110px;
}
.folder .file:not(.create) {
      display: inline-block;
      width: 50%;
}
.folder .file:not(.create) .file-icon, .folder .file:not(.create) .file-detail {
        display: inline-block;
}
}
@media screen and (max-width: 470px) {
.folder .file:not(.create) {
    display: block;
    width: 100%;
}
}
@-webkit-keyframes blink {
from {
    color: rgba(0, 0, 0, 0.4);
    -webkit-box-shadow: 0 0 10px 7px rgba(255, 255, 0, 0.2);
            box-shadow: 0 0 10px 7px rgba(255, 255, 0, 0.2);
}
to {
    color: rgba(0, 0, 0, 0.8);
    -webkit-box-shadow: 0 0 10px 7px rgba(255, 255, 0, 0.8);
            box-shadow: 0 0 10px 7px rgba(255, 255, 0, 0.8);
}
}
@keyframes blink {
from {
    color: rgba(0, 0, 0, 0.4);
    -webkit-box-shadow: 0 0 10px 7px rgba(255, 255, 0, 0.2);
            box-shadow: 0 0 10px 7px rgba(255, 255, 0, 0.2);
}
to {
    color: rgba(0, 0, 0, 0.8);
    -webkit-box-shadow: 0 0 10px 7px rgba(255, 255, 0, 0.8);
            box-shadow: 0 0 10px 7px rgba(255, 255, 0, 0.8);
}
}
@-webkit-keyframes rotation {
from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes rotation {
from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@-webkit-keyframes pulse {
from {
    -webkit-transform: scale(0);
            transform: scale(0);
}
to {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
}
@keyframes pulse {
from {
    -webkit-transform: scale(0);
            transform: scale(0);
}
to {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
}
